// 
.SkeletonDiv_contain{
    height: 1.5rem;
}

.SkeletonDiv_div{
    height: 0.75rem;
    background-color: var(--bg-skeleton);
    border-radius: 20px;
    opacity: 0.25;

    animation: loading 2s infinite alternate;
}

// 
@keyframes loading{
    0%{opacity: 0.25;}
    100%{opacity: 0.1;}
}