//
.pos-rel {
    position: relative;
}

.pos-abs {
    position: absolute;
}

.pos-fixed {
    position: fixed;
}

.pos-sticky {
    position: sticky;
}

//

.pos-abs-0 {
    position: absolute;
    top: 0;
    left: 0;
}

.pos-abs-center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.pos-abs-100 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

//

.pos-fixed-100v {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.pos-fixed-100per {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

//

.pos-sticky-from-header {
    position: sticky;
    top: 56px;
}

/* ------------- */
.bottom-100per {
    bottom: 100%;
}

.bottom-0 {
    bottom: 0;
}

.bottom-50per {
    bottom: 50%;
}

/* ------------- */
.top-100per {
    top: 100%;
}

/* ------------- */
.left-0 {
    left: 0;
}

.left-50per {
    left: 50%;
}

.left-100per {
    left: 100%;
}

/* ------------- */
.right-0 {
    right: 0;
}

.right-100per {
    right: 100%;
}
/* ------------- */
.top-0 {
    top: 0;
}

.top-50per {
    top: 50%;
}

.top-header{
    top: var(--height-header);
}

/* ------ CENTER ------- */

.x-center {
    left: 50%;
    transform: translateX(-50%);
}

.y-center {
    top: 50%;
    transform: translateY(-50%);
}

.top-center {
    top: 0;
    transform: translateY(-50%);
}

/* ------- TRANSFORM ------ */

.top-left-center {
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
}

.top-right-center {
    top: 0;
    right: 0;
    transform: translate(50%, -50%);
}

.bottom-left-center {
    bottom: 0;
    left: 0;
    transform: translate(-50%, 50%);
}

.bottom-right-center {
    bottom: 0;
    right: 0;
    transform: translate(50%, 50%);
}

// ----------

.trans-x-50per {
    transform: translateX(50%);
}

.trans-x--50per {
    transform: translateX(-50%);
}

.trans-x-100per {
    transform: translateX(100%);
}

.trans-x--100per {
    transform: translateX(-100%);
}

// -----------

.trans-y-100per {
    transform: translateY(50%);
}

.trans-y--50per {
    transform: translateY(-50%);
}

.trans-y-100per {
    transform: translateY(100%);
}

// -------- ROTATE

.rotate-45 {
    transform: rotate(45deg);
}

.rotate-90 {
    transform: rotate(90deg);
}

.rotate-180 {
    transform: rotate(180deg);
}

// 

.rotate--45 {
    transform: rotate(-45deg);
}

.rotate--90 {
    transform: rotate(-90deg);
}

/* ------- Z INDEX ------ */

@for $i from -1 through 10 {
    .z-index-#{$i} {
        z-index: $i;
    }
}

.z-index-lv1 {
    z-index: var(--z-index-lv1);
}

.z-index-lv3 {
    z-index: var(--z-index-lv3);
}

.z-index-lv5 {
    z-index: var(--z-index-lv5);
}
